<template>
  <div> <!-- このdivがないとdeveloper toolの方でもid=appのdivが生成されずsvgが一番外の要素になった -->
    <svg width="300" height="300" style="border: solid">
      <template v-for="edge in edgeData">
        <Edge :key="edgeName + edge.edgeId" :edgeData="edge"/>
      </template>
      <template v-for="node in nodeData">
        <NodeCircle :key="nodeName + node.nodeId" :nodeData="node" v-on:node-click="nodeClick" /> <!-- ここでkeyを付けるとうまくいった -->
      </template>
    </svg>
    <div>
      <button v-on:click="test" class="btn">Click</button>
      <!-- <button v-on:click="calc" class="btn2">Calc</button> -->
    </div>
    <p>time : {{time}}</p>
    <p>selectedNodeId : {{selectedNodeId}}</p>
    <p>clickedNum : {{clickedNum}}</p>
    <p>log : {{log}}</p>
  </div>
</template>

<script>
import NodeCircle from "@/components/NodeCircle.vue";
import Edge from "@/components/Edge.vue";
import nodedatas from "@/assets/node_data.json";
import edgedatas from "@/assets/link_data.json";

export default {
  name: "Canvas",
  components: {
    NodeCircle,
    Edge,
  },
  data() {
    return {
      id: 0,
      nodeDatas: nodedatas.NodeDatas,
      edgeDatas: edgedatas.EdgeDatas,
      start: performance.mark("start"),
      end: 0,
      time: 0,
      selectedNodeId: 100,
      neededClickNum: 3,
      clickedNum: 0,
      log: [],
      sendData: []
    }
  },
  computed: {
    nodeData: function () { // nodeData = [{"cx": 222, ..}, {}, {}, ... , {}]
      return this.nodeDatas[this.id].NodeData;
    },
    nodeName: function () {
      return this.nodeDatas[this.id].NodeName;
    },
    edgeData: function() {
      return this.edgeDatas[this.id].EdgeData;
    },
    edgeName: function() {
      return this.edgeDatas[this.id].EdgeName;
    },
    // start: function() {
    //   // return performance.now();
    //   return performance.mark("start");
    // }
  },
  methods: { // この中JavaScriptでかけるっぽい
    test: function () {
      if (this.id == 1) {
        this.id = 0;
      }
      else{
        this.id = 1;
      }
    },
    sendJsonData: function(data) {
      var json_text = JSON.stringify(data);

      const xhr = new XMLHttpRequest;
      xhr.onload = function(){
          var res = xhr.responseText;
          // if (res.length>0) alert(res);
      };
      xhr.onerror = function(){
          alert("error!");
      }
      xhr.open('post', "http://www.vislab.cs.tsukuba.ac.jp/members/yasuda/test1/output.php", true);
      xhr.setRequestHeader('Content-Type', 'application/json');
      xhr.send(json_text);
      console.log(data);
    },
    nodeClick(value) {
      // this.end = performance.now();
      this.end = performance.mark("end");
      // this.time = (this.end - this.start)/1000;
      this.time = performance.measure('fin', 'start', 'end').duration / 1000;
      this.selectedNodeId = value;
      this.log.push(value);
      this.log.push(this.time);
      this.clickedNum++;
      if (this.clickedNum == this.neededClickNum) {
        this.sendData.push(this.log);
        this.sendJsonData(this.sendData);
        this.id++;
        this.clickedNum = 0;
        // this.log.splice(0, this.log.length);
        this.log = [];
        this.sendData = [];
        if (this.id == 2) {
          this.$router.push("/task1-end-home")
        }
      }
      // else {
      //   this.start = performance.now();
      // }
      performance.clearMarks();
      performance.clearMeasures();
      this.start = performance.mark("start");
    },
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  margin-top: 60px;
}
.btn {
  margin-left: 130px;
}
</style>


