<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2 v-show="showText">{{ text }}</h2>
    <button v-on:click="toggle">toggle</button>
  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      text: 'Test Hello',
      showText: false,
    }
  },
  methods: { 
    toggle: function () {
      this.showText = !this.showText
      // this.arr = this.csvToArray('src/assets/test_yasuda.csv') //
      console.log(this.arr)
    },
    parseCSV(data) {
      return data.split('\r\n').map((row) => row.split(','))
    },
    csvToArray(filename) {
      // CSVファイルを文字列として取得
      let srt = new XMLHttpRequest();
      srt.open("GET", filename, false);
      try {
        srt.send(null);
      } catch (err) {
        console.log(err)
      }

      // 配列を用意
      let csletr = [];

      // 改行ごとに配列化
      let lines = srt.responseText.split("\n");

      // 1行ごとに処理
      for (let i = 0; i < lines.length; ++i) {
        let cells = lines[i].split(",");
        if (cells.length != 1) {
          csletr.push(cells);
        }
      }
      return csletr;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



