<template>
  <g class="node" v-on:click="send">
    <circle v-bind:cx="nodeData.cx" v-bind:cy="nodeData.cy" r="10" v-bind:fill="nodeData.nodeFill" class="NODE" v-bind:nodeId="nodeData.nodeId"></circle>
    <text v-bind:x="nodeData.x" v-bind:y="nodeData.y" text-anchor="middle" dy=".35em" v-bind:fill="nodeData.textFill"
      font-weight="normal" style="font-size: 10px;">{{nodeData.nodeId}}</text>
  </g>
</template>

<script>
export default {
  name: 'Circle',
  data () {
    return {}
  },
  props:['nodeData'], // {"cx": "147.23", "cy": "226.048", ..., "textFill": " white" }
  methods: {
    send() {
      this.$emit("node-click", this.nodeData.nodeId);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



