<template>
  <g class="link">
    <line :x1="edgeData.x1" :x2="edgeData.x2" :y1="edgeData.y1" :y2="edgeData.y2"
      :stroke="edgeData.stroke" class="link-from" :edgeId="edgeData.edgeId"></line>
    <line :x1="edgeData.x3" :x2="edgeData.x4" :y1="edgeData.y3" :y2="edgeData.y4"
      :stroke="edgeData.stroke" :edgeId="edgeData.edgeId"></line>
  </g>
</template>

<script>
export default {
  name: 'Edge',
  data () {
    return {}
  },
  props:['edgeData'], // {"cx": "147.23", "cy": "226.048", ..., "textFill": " white" }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


