import Vue from "vue";
import Router from "vue-router";
import Canvas from "@/Components/Canvas"
import HelloWorld from "@/components/HelloWorld"
import Home from "@/components/Home"
import Task1StartHome from "@/components/task1/Task1StartHome"
import Task1EndHome from "@/components/task1/Task1EndHome"

Vue.use(Router)

export default new Router({
  mode: 'history',                  
  // base: process.env.BASE_URL,
  base: '/vue_app_test/',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/task1-canvas',
      name: 'Canvas',
      component: Canvas
    },
    {
      path: '/routing',
      name: 'routing',
      component: HelloWorld
    },
    {
      path: '/task1-start-home',
      name: 'task1-home',
      component: Task1StartHome
    },
    {
      path: '/task1-end-home',
      name: 'task1-end-home',
      component: Task1EndHome
    }
  ]
})


